<script>
  import Header from "@/components/Header.vue";
  import Footer from "@/components/Footer.vue";
  import https from "@/https.js";
  import $ from 'jquery'

  export default {
    data() {
      return {
        cur: 0, //默认选中第一个tab
        layout: 1,
        begin: 1,
        end: 8,
        size: 8,
        userList:[],
      }
    },
    mounted:function(){
      let params ={begin: this.begin,end:this.end};
        https.fetchPost('/video/own',params)
          .then(data =>{
            console.log(data.data.data)
            this.userList = data.data.data
            for (var i=0;i<this.userList.length;i++) {
              this.userList[i].videoCover = "api" + this.userList[i].videoCover
            }
          })
    },
    methods:{
      play:function(i){
        this.$router.push({path:'/videodetail',query:{videoinfo: JSON.stringify(this.userList[i])}});
      }
    }

  }
</script>
